.postcard {
  width: 100%;
  max-width: 540px;
  background: rgba(59, 59, 59, 0.479);
  border-radius: 10px;
  /* border: 1px solid #000; */
  margin-top: 30px;
  padding-right: 30px;
  box-shadow: 0 0 5px rgb(27, 224, 250);
}

.post {
  position: relative;
  top: -55px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-end;
}

.post__content {
  max-width: 390px;
  color: rgb(245, 245, 6);
  font-family: serif;
  font-size: 18px;
}

.post__img {
  width: 100%;
  max-width: 390px;
  border-radius: 10px;
}

.author {
  display: flex;
  align-items: center;
}

.author__avatar-img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin: 35px 25px 15px 30px;
  box-shadow: 0 0 5px rgb(228, 250, 27);
}

.author-info {
  display: flex;
  justify-content: space-between;
}

.author-info__name {
  font-size: 21px;
  margin-right: 15px;
  font-family: serif;
  font-weight: bold;
  color: yellow;
  text-shadow: 0 0 5px rgb(228, 250, 27);
  transition: text-shadow 0.5s ease;
}

.author-info__name:hover {
  text-shadow: 0 0 20px rgb(228, 250, 27);
}

.author-info__nickname {
  color: rgb(121, 119, 119);
  font-style: italic;
  font-size: 21px;
  margin-right: 15px;
  transition: text-shadow 0.5s ease;
}

.author-info__nickname:hover {
  text-shadow: 0 0 20px rgb(228, 250, 27);
}

.author-info__date {
  color: rgb(121, 119, 119);
  font-size: 14px;
  line-height: 2;
}
